'use client'

import React, { useState } from "react";
import Image from "next/image";
import { urlForImage } from "../sanity/client";
import { Bodoni_Moda, Lato } from "next/font/google";
import { type SanityDocument } from "next-sanity";
import { client } from "../sanity/client";
import PersonGalleryTwo from "../components/PersonGalleryTwo";

const bodoni = Bodoni_Moda({
  subsets: ["latin"],
  display: "swap",
});
const lato = Lato({
  weight: ["300", "700"],
  style: ["normal", "italic"],
  subsets: ["latin"],
});

const PERSON_QUERY = `*[_type == "person" && order < 13] | order(order asc) | { _id, name, title, subtitle, description, image, slug, order }`;
const PERSON_GALLERY_QUERY = `*[_type == "person"] | order(order asc) { _id, name, title, subtitle, description, image, slug, order, gallery[] { asset->{ _id, url }, caption } }`;

const options = { next: { revalidate: 30 } };

export default async function Team() {
  const persons = await client.fetch<SanityDocument[]>(PERSON_QUERY, {}, options);
  const personsGallery = await client.fetch<SanityDocument[]>(PERSON_GALLERY_QUERY, {}, options);

  const [selectedPerson, setSelectedPerson] = useState(personsGallery[0]);

  const handlePersonClick = (person) => {
    const matchedPerson = personsGallery.find((p) => p._id === person._id);
    if (matchedPerson) {
      setSelectedPerson(matchedPerson);
    }
  };

  return (
    <div className="mt-8 text-center mx-auto max-w-7xl">
      <div className="mx-auto max-w-3xl">
        <h3 className="text-xl mx-10 md:text-2xl font-semibold text-gray-700">
          An Experienced and Dedicated Team at Your Service
        </h3>
        <div className="h-[3px] w-[120px] bg-red-500 rounded mx-auto mt-4"></div>
      </div>

      {/* Dynamic Gallery */}
      <PersonGalleryTwo person={selectedPerson} />

      {/* Team Member Cards */}
      <div className="mt-16 grid grid-cols-1 sm:grid-cols-2 md:grid-cols-3 lg:grid-cols-4 mx-auto max-w-6xl px-12">
        {persons.map((person) => (
          <div
            key={person._id}
            className="cursor-pointer block pb-4"
            onClick={() => handlePersonClick(person)}
          >
            <div className="px-4 text-center mb-4">
              <div className={bodoni.className}>
                <div className="relative w-40 h-40 mx-auto">
                  <Image
                    src={urlForImage(person.image).quality(100).url()}
                    alt={person.name}
                    width={200}
                    height={100}
                    className="rounded-full object-cover"
                  />
                </div>
                <p className="font-semibold text-xl mt-4">{person.name}</p>
                <div className={lato.className}>
                  <p className="italic mt-2 text-gray-600 text-sm">{person.title}</p>
                  <p className="text-gray-600 text-sm">{person.subtitle}</p>
                </div>
              </div>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
}
